import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { MovieService } from '../../../shared-module/movie.service';
import { MovieModel } from '../../../shared-module/models/movie-model';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-movie-list-page',
  templateUrl: './movie-list-page.component.html',
  styleUrls: ['./movie-list-page.component.scss']
})
export class MovieListPageComponent implements OnInit, OnDestroy {
  private page = 1;
  private movieService: MovieService;

  movies: MovieModel[];
  loading = false;
  private subs: Subscription = new Subscription();

  constructor(movieService: MovieService) {
    this.movieService = movieService;
  }

  ngOnInit() {
    this.loading = true;

    this.subs.add(this.movieService.fetchList(this.page).subscribe(data => {
      this.movies = data;
      this.loading = false;
    }));
  }

  ngOnDestroy() {
     this.subs.unsubscribe();
  }
}
